export default {
	data() {
		return {
			isok: true,
			alipayNum: '',
			count:0,
			userName:'',
			authCode:'',

		};
	},
	methods: {
		//  确认添加，返回上一页，查询当前账号
		sureAdd() {
			uni.navigateBack({});
		},
		getCode() {
			const TIME_COUNT = 60;
			let timer;
			if (!timer) {
				this.count = TIME_COUNT;
				timer = setInterval(() => {
					if (this.count > 0 && this.count <= TIME_COUNT) {
						this.count--;
					} else {
						clearInterval(timer);
						timer = null;
					}
				}, 1000);
			}
			
			//  获取验证码
			this.$api.getAuthCode(this.alipayNum)
			
		}
	}
};
